@font-face {
  font-family: 'MontSerrat';
  src: url('/public/fonts/MontserratAlternates-ExtraBold.ttf') format('truetype');
}

@font-face {
  font-family: 'Arcade';
  src: url('/public/fonts/Market_Deco.ttf') format('truetype');
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;

}

#Ixh5Y5je5vRh7EHlEwVvX:hover .rectModal {
  stroke: #32C267 !important
}

#Ixh5Y5je5vRh7EHlEwVvX:hover .lienGit {
  fill: #32C267 !important
}

.contactMe {
  font-family: Arcade;
}

:root {
  --primary-color: #2D2D2D;
  --secondary-color: #32C267;
  --color: white
}




* {
  margin: 0;
  padding: 0;

}

body {
  background-color: black;
  -ms-overflow-style: none;
  /* IE et Edge */
  scrollbar-width: none;
  /* Firefox */
  overflow: hidden;
}

html {
  font-size: 16px;
}



#image-container {
  width: 100%;
  height: 100%;
}


.svgDessin {



  stroke-dasharray: 1500px;
  stroke-dashoffset: 1500px;
  stroke: white;



}


header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 61px;
  padding: 0 0.5rem;

}

#logo {
  font-size: 2.2rem;
  font-family: 'MontSerrat';
  color: var(--primary-color);
  cursor: default
}

#sousHeader {
  display: flex;
  flex-direction: row;
  position: relative;
  height: calc(100vh - 61px);
  align-items: end;

}

.rond {
  height: 10px;
  width: 10px;
  background-color: black;
  border-radius: 100%;
}

.rond:nth-child(1) {
  animation: descende 1s linear infinite
}

.rond:nth-child(2) {
  animation: descende 1s linear infinite;
  animation-delay: 0.35s;
}

.rond:nth-child(3) {
  animation: descende 1s linear infinite;
  animation-delay: 0.65s;
}

@keyframes descende {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-2px);
  }

  100% {
    transform: translateY(0);
  }
}

.blocPhoto {
  order: 1;
  /*height: 68vh;*/

  height: 100%;
  display: flex;
  justify-content: end;
  width: clamp(450px, 63vw, 600px);

}

.ancre {
  transition: transform 0.3s linear;
  transform: translate3d(0, 0, 0);
  cursor: pointer;
  color: black;
  text-decoration: none;
}

.ancre:hover {
  transform: translate3d(11%, 0, 0);

}

.blocTitre {
  order: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  justify-content: space-between;
  position: absolute;
  width: max-content;
  left: 0
}

.blocTitre>:first-child {
  display: flex;
  flex-grow: 1;
  align-items: center;
  margin-top: 2vw
}

.blocTitre>:nth-child(2) {
  margin-bottom: 2vw;
}

.blocTitre>:nth-child(2)>svg {
  width: 3vw;
}

.hiddenProjet {
  width: 0;
  transform: scale3d(0, 0, 0)
}

.visibleProjet {
  width: auto;
  transition: transform 0.7s linear;
  transform: scale3d(1, 1, 1);
}

#image-canvas {


  height: 100%;
  z-index: 2;
  position: relative;


}

#containerMatrix {

  display: none;
}

#canvasMatrix {

  display: none;
}

#accueil {
  position: relative;
  height: 850px;


}

#sousAccueil {
  position: sticky;
  top: 0;
  background-color: rgb(255, 255, 255);
  transition: transform 0.25s linear;
  overflow: hidden;
  will-change: transform;

}

#arrowBottom {
  animation: descend 1.2s ease-in-out infinite;
  fill: var(--primary-color);
  width: clamp(15%, 8vw, 22%);
  margin: auto;
  display: block;
  margin-top: 1rem;
}


@keyframes descend {
  0% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}

#titreAccueil {
  font-size: clamp(2.4rem, 10vw, 4rem);
  padding-left: 5vw;
  color: var(--primary-color);
  cursor: default
}

#sectionModal {
  margin-top: 6rem;
  margin-bottom: 6rem;
}

.blocModal {
  display: none;
}



.blocSvg {
  display: flex;
  flex-direction: column;
  gap: 1.7rem;
  padding: 0 1rem;
  cursor: default
}


#sectionProjets {
  color: white;
  overflow: hidden;

}

#titreProjet {
  font-size: 11vw;
  font-weight: bold;
  text-align: center;
  /*margin:7rem 0;*/

  font-family: 'Arcade';
  cursor: default
}

.projets {
  display: flex;
  justify-content: space-between;
  align-items: start;
  cursor: pointer;
  padding: 1.7rem 0
}


.stars {
  opacity: 0;
  animation: opacityStars 1s linear;
}

@keyframes opacityStars {
  from {
    opacity: 0
  }

  to {
    opacity: 1
  }
}

#blocProjets {
  padding: 0 1rem;
  margin: 6rem 0 6rem 0;
}

.sousTitreProjet {
  width: 50%;
}

.hoverProject {
  background-color: white;
}

.projets:hover+.hoverProject {

  background-color: #32C267;
}

.projets:hover {
  color: #32C267
}

.projets:hover .techno {
  border: 1px solid #32C267 !important
}

.sousTitreProjet2 {
  width: 50%;
  display: flex;
  flex-wrap: wrap;
  line-height: 1em;
  justify-content: end;
  align-items: start;
  gap: 0.5rem
}

.sousTitreProjet>p {
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: bold;
  line-height: 2rem;

}

.sousTitreProjet>span {

  font-size: 0.95rem;
  color: lightslategrey;
  /*margin-bottom: 1.5rem;*/
  display: block;
}

.techno {
  font-size: 1.1rem;
  text-transform: uppercase;
  border: 0.1rem solid white;
  border-radius: 1rem;
  padding: 0.3rem
}

.projets:first-child {

  /*padding-top:2rem*/
}

#moon {
  transform: perspective(300px) rotateX(42deg) translateY(80px);
  margin-left: 0.5rem;
  height: 16rem;
  /* Ajustez les valeurs selon vos besoins */
  width: 100%;
  opacity: 83%;
  filter: drop-shadow(0 -0.5rem 1.3rem rgb(255, 255, 255));
  position: absolute;
  bottom: 0;
  left: 0;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.image-spin-infinite {
  animation: spin 170s linear infinite;
}

.inputContact {
  background: none;
  border: none;
  border-bottom: 1px solid white;
  color: white;
  width: 40%;
  font-size: 1rem;
  outline: none;
}

.inputContactObjet {
  width: 100% !important;
}

textarea {
  background: none;
  border: 1px solid white;
  width: 100%;
  height: 15rem;
  color: white;
  font-size: 1rem;
  outline: none;

}

.blocRond {
  display: none;
}

.btnForm {
  align-self: end;
  font-size: 1.1rem;
  border: none;
  border-radius: 0.2rem;
  font-weight: bold;
  cursor: pointer;
  background-color: white;
  width: 127px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;

}

.btnForm:hover {

  background-color: rgb(211, 210, 210);
}


#sectionContact {
  display: flex;
  overflow: hidden;
  position: relative;
  justify-content: space-between;
  height: 100vh;

  flex-direction: column;
}

.blocChargement1,
.blocChargement2 {
  width: 50%;
  height: 100%;
  background-color: white;
  display: flex;
  align-items: center;
}

.letterLoad1,
.letterLoad2 {
  display: block;
  width: 100%;
  font-size: 11vw;
  font-family: MontSerrat;
}

.letterLoad1 {
  text-align: end;
}




form {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 2;

  gap: 2rem;
  width: 70%;


}

.moonMan {
  width: clamp(150px, 40vw, 35vw);
  top: -2%;
  right: -20px;
  position: absolute;
  opacity: 0;

}

.blocGaucheContact {
  display: flex;
  justify-content: center;
  flex-grow: 1;
  align-items: center;
  margin-top: 5rem;
}

.blocInfoMob {
  display: flex;
  flex-direction: column;
  align-items: end;
  padding: 0 1rem 0.5rem 1rem
}

.blocDroitContact {
  display: none;
}

form>div {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

#burger {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.35rem;
  cursor: pointer;
  position: relative;
  z-index: 4;
}


.rowBurger {
  height: 0.27rem;
  width: 2.2rem;
  background-color: black;
  border-radius: 0.5rem;
  position: relative;
  z-index: 5;
}

#containerBurger {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  clip-path: circle(0 at 100% 0);
  transition: clip-path 0.3s linear;
  background-color: var(--secondary-color);
  z-index: 3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

}

.expanded {
  clip-path: circle(100%) !important;

}

.ancre {
  font-size: 2rem;
  display: inline-block;
}



#navMenu {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  margin-top: 2.2rem;
  padding-left: 0.6rem;
  font-weight: bold;
}

.coordonnees {
  display: flex;
  flex-direction: column;
  align-self: end;
  font-size: 1.2rem;
  font-weight: bold;
}

#blocInformations {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
  padding: 0 0.5rem
}

.reseauSociaux {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

ul {
  list-style: none;
}

.reseau {
  width: 2.1rem;
  height: 2.1rem;

}

.containeAnime {
  overflow: hidden;
}

.anime {
  transform: translateY(110%);
  transition: transform 0.3s linear;
  transition-delay: 0.1s;
}

.animeTransition {
  transform: translateY(0);

}

.coordonnee {
  display: block;
}

.firstRow {
  transform: rotate(45deg) translateY(0.40rem);
}

.thirdRow {
  transform: rotate(-45deg) translateY(-0.40rem);
}

.secondRow {
  display: none;

}

.rowBurger {
  transition: transform 0.1s linear;

}



.teste {

  position: absolute;
  top: 0;
  left: 0;
  display: none;
  transition: transform 0.1s linear;
  pointer-events: none;
  padding: 1rem;
  border: 1px solid #ffffff;
  border-radius: 10%;
  background-color: black;
}


@media only screen
/*tablette landscape*/
and (min-width: 568px) and (max-width: 1280px) and (orientation: landscape) {


  .textContact {
    font-size: 1.5vw;
  }

  .moonMan {
    right: -23px;
    top: -20px;
    width: clamp(10vw, 20vw, 30vw);
  }

  #sectionContact {
    flex-direction: row;
  }

  .blocDroitContact {
    display: flex;
    align-self: center;
    width: 50%;
    justify-content: center;
  }

  .blocGaucheContact {
    width: 50%;
  }

  .blocInfoMob {
    display: none;
  }

  #containerMatrix {
    display: none;
  }

  #blocProjets {
    padding: 0 4vw
  }

  #titreContact {
    padding: 0 7vw;
  }


  .sousTitreProjet>p {
    font-size: 4vw;
    margin-bottom: 0.5em;
  }

  .sousTitreProjet>span {
    font-size: 1em;
  }

  #arrowBottom {
    width: 20%;
  }

  header {
    padding: 0 1rem
  }

  .blocSvg {
    padding: 0 25%
  }

  form {
    /* padding:0 23vw*/
  }


  #moon {
    height: 16rem;
  }

  #titreAccueil {
    font-size: clamp(4rem, 5vw, 40rem);
  }

  textarea {
    height: 15rem;
  }

  #titreProjet {
    text-align: left;
    margin-left: 2.4rem;
  }

}




@media only screen and (max-width: 926px) and (orientation: landscape) and (max-height:428px) {
  /* vue mobile landscape*/


  html {
    font-size: 12px;
  }

  #sectionContact {
    height: auto;
  }

  .blocInfoMob {
    display: none;
  }

  .moonMan {
    right: -7%;

  }

  .blocDroitContact {
    margin-top: 1rem;
  }


  #titreAccueil {
    font-size: 6.5vw;
  }

  .blocTitre {
    /* top:25%*/
  }

  #sectionModal {
    margin-top: 2rem;
  }

  #arrowBottom {
    margin-top: 5rem;
    width: 11.5%;
  }

  .blocSvg {
    padding: 0 12rem;
  }

  form {
    padding: 0 5rem;
  }

  textarea {
    height: 15rem;
  }

  .inputContact,
  textarea,
  .btnForm {
    font-size: 1.3rem;
  }

  #moon {
    height: 18rem;
  }

  .btnForm {
    padding: 0.4rem 2.5rem
  }

  .blocPhoto {
    width: 50%;
    display: 'flex';
    justify-content: center;
  }

  #image-container {
    width: 80%;
  }

  #image-canvas {
    width: 100%;
  }

  #containerMatrix {
    display: none;
  }

}




@media only screen
/*tablette portrait*/
and (min-width: 768px) and (max-width: 1081px) and (orientation: portrait) and (-webkit-min-device-pixel-ratio: 1) {

  #containerMatrix {
    display: none;
  }

  #sectionModal {
    margin-top: 30rem;
  }

  textarea {
    height: 17rem;
  }


  #sectionContact {
    height: 100vh;
  }

  .blocInfoMob {
    margin-top: 0;
  }

  #arrowBottom {
    width: 20%;
  }

  header {
    padding: 0 1rem
  }

  .blocSvg {
    padding: 0 10rem
  }


  #moon {
    height: 20rem;
  }

  #titreProjet {
    text-align: left;
    margin-left: 1rem;
  }

}



@media only screen and (min-width: 1281px) {

  .blocDroitContact,
  .blocGaucheContact {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;


  }


  .textContact {
    font-size: 1rem;
  }

  .moonMan {

    top: -35px;
    right: -40px;
    width: 36vh !important;


  }

  #sectionContact {
    flex-direction: row;
  }

  .blocInfoMob {
    display: none;
  }

  #accueil {
    height: 1050px
  }

  #titreProjet {

    text-align: left;
    margin-left: 1.2rem;
  }

  #containerMatrix {
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    width: 28%;
    display: block;
    z-index: 3;
  }



  #canvasMatrix {
    height: 100%;
    width: 100%;
    position: relative;

    display: block;
    background-color: transparent;
  }


  .blocModal {
    display: flex;
    width: 50%;
    height: 100vh;
    position: sticky;
    top: 0;
    align-items: center;
  }

  .blocSvg {
    width: 36%;
    padding-left: 14%;
  }

  #sectionModal {
    display: flex;
    margin-top: 20rem;
    position: relative;
  }

  .blocPhoto {
    width: 50%;
    display: flex;
    justify-content: center;
  }

  form {}

  #image-container {
    width: 95%;
  }




  #moon {
    height: 15rem;
  }

  #arrowBottom {
    width: 20%;
  }



  #titreAccueil {
    font-size: clamp(5vw, 5vw, 40rem);
    padding-left: 2rem;
  }

  #sousHeader {
    flex-direction: row;
    align-items: center;
  }

  .blocTitre {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    position: relative;
    width: 100%;
    order: 1;
    height: 100%;
    top: 0;

  }

  .blocTitre>:first-child {
    display: flex;
    flex-grow: 1;
    align-items: center;
    margin-top: 2vw
  }

  .blocTitre>:nth-child(2) {
    margin-bottom: 2vw;
  }

  .blocTitre>:nth-child(2)>svg {
    width: 3vw;
  }


  #sectionProjets {
    padding: 0 10rem
  }

  #blocProjets {
    margin: 10rem 0 10rem 0;

  }

  .projets {
    padding: 2.5rem 0
  }

  .sousTitreProjet>p {
    font-size: 3.7vw;
    margin-bottom: 0.5em;
  }

  .sousTitreProjet>span {
    font-size: 1em;
  }

  #titreContact {


    padding-left: 18rem;
  }

  #titreProjet {
    font-size: 7rem;


  }


  textarea {
    height: 23rem;
  }



  .imageProjet {
    height: 100%;
    width: 100%;
  }

}


