#containerMatrix {
  height: 100% !important;
  position: absolute !important;
  top: 0 !important;
  right: 0 !important;
  width: 28% !important;
  display: block !important;
  z-index: 3 !important;
}



#canvasMatrix {
  height: 100% !important;
  width: 100% !important;
  position: relative !important;
  /*z-index: 3;*/
  display: block !important;
  background-color: transparent !important;
}


.blocSuccess {
  background-color: white;
  position: absolute;
  bottom: 0;
  left: 3%;
  font-weight: bold;
  font-size: 1.3rem;
  padding: 1.5rem 2rem;
  border-radius: 0.5rem;
  transform: translateY(80px);
}

@media only screen and (max-width: 428px) {
  .blocSuccess {
    left: 0;
    right: 0;
    margin: auto;
    transform: translateY(100px);
    width: 60%;
    font-size: 1rem;
    text-align: center;
  }
}